import { model, Schema, Document } from 'mongoose';
import { Employee } from '@interfaces/employee.interface';

const employeeSchema: Schema = new Schema({
  id: {
    type: String,
    required: true,
    unique: true,
  },
  firstName: {
    type: String,
    required: true,
  },
  lastName: {
    type: String,
    required: true,
  },

  email: {
    type: String,
    required: true,
    unique: true,
  },
  number: {
    type: String,
    required: true,
  },
  gender: {
    type: String,
    required: true,
  },
  photo: {
    type: String,
    required: true,
  },
});

const userModel = model<Employee & Document>('Employee', employeeSchema);

export default userModel;
